@import "../../bootstrap/functions";
@import "../../bootstrap/variables"; 
@import "../../bootstrap/mixins";
@import "../../mixins";


.cm-modal-body{
	font-weight: 400;
	display: block;
	font-size:16px;
	color:#333;
	line-height: 1.5;
	hr{
		display: block;
		width: 100%;
		height: 0;
		margin: 20px 0;
		border: 0;
		border-top: 2px solid #f4f3f1;
	}
	h1,h2,h3,h4,h5,h6{
		position: relative;
		margin:0;
		line-height: 1.5;
	}
	.color-green{
		color:#00A94F;
	}
	.color-orange{
		color:#DE7900;
	}
	.color-grey{
		color:#999;
	}
	.color-important{
		color:#f9556f;
	}
	.align-left{
		text-align:left;
	}
	.align-center{
		text-align:center;
	}
	.align-right{
		text-align:right;
	}
	.w-full{
		width: 100%;
		max-width: 100%;
	}
	.w-half{
		width: 49%;
		max-width: 49%;
	}
	.w-third{
		width: 32%;
		max-width: 32%;
	}
	.w-quarter{
		width: 24%;
		max-width: 24%;
	}
	.w100p{
		width: 100%;
		max-width: 100%;
	}
	.w50p{
		width: 50%;
		max-width: 50%;
	}
	.w33p{
		width: 33%;
		max-width: 33%;
	}
	.w25p{
		width: 25%;
		max-width: 25%;
	}
	a{
		opacity:1;
		color:#00A94F;
		text-decoration: underline;
		word-break: break-all;
		@media (hover: hover) and (pointer: fine) {
			&:hover{
				opacity:1;
				color:#53b96a;
			}
		}
		&:active{
			opacity:1;
			color:#53b96a;
		}
	}

	h1{

	}
	h5,.h5{
		font-size:20px;
		padding-left:15px;
		font-weight: 400;
		line-height: 1.3;
		margin-bottom: .5rem;
		&:before{
			content:'';
			position: absolute;
			top:0;
			left:0;
			width: 6px;
			height: 100%;
			background-color: #00A94F;
			border-radius: 2px;
		}
	}
	h6,.h6{
		font-size:18px;
		font-weight: 400;
	}
	p{
		margin-bottom: .5rem;
	}
	.small{
		font-size:14px;
		line-height: 1.4;
	}
	p.small{
		color:#666666;
	}
	ul,ol{
	    list-style: none;
    	padding: 0 0 0 1.5rem;
		margin: 0 0 1.5rem 0;
	}
	ol li{
		list-style:decimal;
	}
	ul{
    	li{
    		position: relative;
    		&:before{
    			content:'';
    			position: absolute;
    			display: block;
    			width:5px;
    			height: 5px;
    			top:.6rem;
			    left: -1rem;
			    border-radius: 50%;
    			background-color: #00A94F;
    		}

			+li{
				margin-top: .5rem;
			}
    	}
    	// &.small{
		// 	color:#666666;

		// 	li{
	    // 		&:before{
	    // 			background-color: #999;
		// 	    	left: -1rem;
	    // 		}
		// 	}
    	// }
	}

	table{
		width: 100%;
		border-collapse: collapse;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
		overflow:hidden;
	}
	th,td{
		padding: .75rem;
		vertical-align: top;
		border: 2px solid #fff;
	}
	th{
		background-color: #00A94F;
		color:#fff;
	}
	td{
		background-color: #EFEFEF;
		
	}
	thead{
	}
	img{
		// width: 100%;
		max-width: 100%;
		margin:0 auto;
		// display:block;
		margin-bottom: 1.5rem;
	}
	.cm-modal-btn{
		margin:10px;
	}
}

