@mixin btn_icon($icon:'',$arrow-color:#fff,$arrow-size:8px,$arrow-width:12px,$arrow-height:1px,$arrow-border:2px,$right:15px){
  @if $icon == 'arrow-right' {

    padding-right: 50px;
    position: relative;
    
    min-width: 130px;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
      min-width: 187px;
    }
    &:hover{
      span{
        &:before {
          right: $right - $arrow-border + 1px - 3px;
        }
        &:after {
          right: $right - 4px;
        }
      }
    }
    span{
      &:after, &:before {
        content: "";
        display: block;
        position: absolute;
        transition:right .1s;
      }
      &:before {
        top: 50%;
        right: $right - $arrow-border + 1px;
        width: $arrow-size;
        height: $arrow-size;
        transform-origin: 50% 50%;
        transform:  translate(0, -($arrow-size / 2) + 1px) rotate(45deg);
        box-shadow: inset -#{$arrow-border} $arrow-border 0 0 $arrow-color;
        border-radius: 0 $arrow-size*0.15 0 0;
      }
      
      &:after {
        top: 50%;
        right: $right;
        width: $arrow-width;
        height: $arrow-height;
        transform:  translate(0, -($arrow-border / 2) + 1px);
        background-color: $arrow-color;
      }
    }
  }

}

@mixin btn_color($color:'transparent') {

  @if $color == 'transparent' { 
    
    color: #555 !important;
    border: 1px solid #ddd;
    
    @media (hover: hover) and (pointer: fine) {
      &:hover{
        text-decoration: none;
        opacity: 1;
        color: #555 !important;
        background-color: rgba(0,0,0,.1);
        border-color: #fff;
      }
    }
    &:active{
      opacity: 1;
      background-color: rgba(0,0,0,.1);
      border-color: #fff;
    }
  }
  @else if $color == 'white' {
    
    color: #555 !important;
    background: #fff;
    border: 1px solid #ddd;
    
    @media (hover: hover) and (pointer: fine) {
      &:hover{
        text-decoration: none;
        opacity: 1;
        color: #00a94f !important;
        background-color: #f2f2f2;
        border-color: #ddd;
      }
    }
    &:active{
      opacity: 1;
      color: #00a94f !important;
      background-color: #f2f2f2;
      border-color: #ddd;
    }
  }
  @else if $color == 'orange' {
    
    color: #fff !important;
    background: #f4921e;
    border: 1px solid #f4921e;


    @media (hover: hover) and (pointer: fine) {
      &:hover{
        text-decoration: none;
        opacity: 1;
        color: #fff !important;
        background-color: #d5780a;
        border-color: #d5780a;
      }
    }
    &:active{
      opacity: 1;
        color: #fff !important;
        background-color: #d5780a;
        border-color: #d5780a;
    }
  }
  @else if $color == 'green' {
    
    color: #fff !important;
    background: #00a94f;
    border: 1px solid #00a94f;


    @media (hover: hover) and (pointer: fine) {
      &:hover{
        text-decoration: none;
        opacity: 1;
        color: #fff !important;
        background-color: #007637;
        border-color: #007637;
      }
    }
    &:active{
      opacity: 1;
        color: #fff !important;
        background-color: #007637;
        border-color: #007637;
    }
  }
}

@mixin btn_size($size:'m') {
    @if $size == 's' { 
        font-size: 16px;
        // line-height: 1.3;
        line-height:1;
        padding:0 28px;
        height:42px;
        min-width: 130px;
        @media (min-width:map-get($grid-breakpoints, 'lg')){
          min-width: 170px;
        }
        // 想增加按鈕規則，在原本的按鈕規則下，lg 有箭頭最短寬度 >= 187px，lg 最短寬度 >= 170px，sm 最短寬度 >= 130px 

    }
    @else if $size == 'm' {
          font-size: 20px;
          // line-height: 1.3;
          line-height:1;
          padding: 0 40px;
          height:48px;
          @media (min-width:map-get($grid-breakpoints, 'lg')){
            min-width: 170px;
          }
    }
    @else if $size == 'xs' {
          font-size: 16px;
          line-height:1;
          padding: 0 18px;
          height:32px;
          @media (min-width:map-get($grid-breakpoints, 'lg')){
            min-width: 170px;
          }
    }
}
