@import "../../bootstrap/functions";
@import "../../bootstrap/variables"; 
@import "../../bootstrap/mixins";
@import "../../mixins";

// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and stuff
$modal-content-border-radius:10px; 
$modal-header-padding:16px;

.cm-modal-open {
  // Kill the scroll on the body
  overflow: hidden;
  .cathay-comp.cm-modal {
    overflow-x: hidden;
    overflow-y: auto;
  }
}

// Container that the modal scrolls within

.cathay-comp.cm-modal {
	

	font-family: "Microsoft JhengHei", "微軟正黑體", "STHeitiTC-Light", "Microsoft YaHei", "微软雅黑", "新細明體", Arial, sans-serif;

  position: fixed;
  top: 0;
  left: 0;
  z-index: $zindex-modal;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity:1;
  z-index: 9999;
  outline: 0;

  background: rgba(0, 0, 0, 0.75);
  transition: opacity 0.3s ease 0.1s;
  &.fade{
  	opacity:0;

  	&.show{
  		opacity:1;
  	}
  }
  .fade {
	//   @include transition($transition-fade);

	  &:not(.show) {
	    opacity: 0;
	  }
	}

	.collapse {
	  &:not(.show) {
	    display: none;
	  }
	}

	.collapsing {
	  position: relative;
	  height: 0;
	  overflow: hidden;
	  @include transition($transition-collapse);
	}

	.cm-modal-close {

	}


	button.cm-modal-close {
	  appearance: none;
	}


	a.cm-modal-close.disabled {
	  pointer-events: none;
	}


}

.cathay-comp{
	
	.cm-modal-dialog {
	  position: relative;
	  width: auto;
	  margin: 1.75rem auto;
	  // allow clicks to pass through for custom click handling to close modal
	//   pointer-events: none;

	  // When fading in the modal, animate it to slide down
	  .cm-modal.fade & {
	    @include transition($modal-transition);
	    transform: $modal-fade-transform;
	  }
	  .cm-modal.show & {
	    transform: $modal-show-transform;
	  }
	}
	.cm-modal-dialog-scrollable {
	  display: flex; // IE10/11
	  max-height: calc(100% - #{$modal-dialog-margin * 2});

	  .cm-modal-content {
	    max-height: calc(100vh - #{$modal-dialog-margin * 2}); // IE10/11
	    overflow: hidden;
	  }

	  .cm-modal-header,
	  .cm-modal-footer {
	    flex-shrink: 0;
	  }

	  .cm-modal-body {
	    overflow-y: auto;
	  }
	}

	.cm-modal-dialog-centered {
	  display: flex;
	  align-items: center;
	//   min-height: calc(100% - #{$modal-dialog-margin * 2});
	
	//   min-height: calc(100% - 170px);

	  // Ensure `modal-dialog-centered` extends the full height of the view (IE10/11)
	  &::before {
	    display: block; // IE10
	    // height: calc(100vh - 170px);
	    content: "";
	  }

	  // Ensure `.modal-body` shows scrollbar (IE10/11)
	  &.cm-modal-dialog-scrollable {
	    flex-direction: column;
	    justify-content: center;
	    height: 100%;

	    .cm-modal-content {
	      max-height: none;
	    }

	    &::before {
	      content: none;
	    }
	  }
	}

	// Actual modal
	.cm-modal-content {
	  position: relative;
	  display: flex;
	  flex-direction: column;
	  max-width: none;
	  transform:none;
	  opacity:1;
	  // box-shadow:none;
	  // transition:none;
	  width: 100%; // Ensure `.modal-content` extends the full width of the parent `.modal-dialog`
	  // counteract the pointer-events: none; in the .modal-dialog
	  color: $modal-content-color;
	  pointer-events: auto;
	  background-color: $modal-content-bg;
	  background-clip: padding-box;
	  border-radius:$modal-content-border-radius;
	//   overflow:hidden;
	  @include box-shadow($modal-content-box-shadow-xs);
	  // Remove focus outline from opened modal
	  outline: 0;
	}

	// Modal background
	.cm-modal-backdrop {
	  position: fixed;
	  top: 0;
	  left: 0;
	  z-index: $zindex-modal-backdrop;
	  width: 100vw;
	  height: 100vh;
	  background-color: $modal-backdrop-bg;

	  // Fade for backdrop
	  &.fade { opacity: 0; }
	  &.show { opacity: $modal-backdrop-opacity; }
	}

	// Modal header
	// Top section of the modal w/ title and dismiss
	.cm-modal-header {
	  display: flex;
	  position:relative;
	  align-items: flex-start; // so the close btn always stays on the upper right corner
	  justify-content: space-between; // Put modal header elements (title and dismiss) on opposite ends
	  padding:15px;
	  background-color: #4ba659;
	  @include border-top-radius($modal-content-border-radius);

	//   .cm-modal-close {
	// 	  position: absolute;
	//   }
	  
	  @include media-breakpoint-up(lg) {
		padding:16px;
	  }
	}

	// Title text within header
	.cm-modal-title {
	  margin: 0;
	  line-height: $modal-title-line-height;
	  font-size:18px;
	  font-weight: 400;
	  width: 100%;
	  letter-spacing: 2px;
	  color:#fff;
	  text-align:center;
	  @include media-breakpoint-up(lg) {
		font-size:20px;
	  }
	}

	// Modal body
	// Where all modal content resides (sibling of .modal-header and .modal-footer)
	.cm-modal-body {
	  position: relative;
	  // Enable `flex-grow: 1` so that the body take up as much space as possible
	  // when should there be a fixed height on `.modal-dialog`.
	  flex: 1 1 auto;
	//   padding: $modal-inner-padding;
		padding:16px;
		
		@include media-breakpoint-up(lg) {
			padding:24px;
		}
	}

	// Footer (for actions)
	.cm-modal-footer {
	  display: flex;
	  flex-wrap: wrap;
	  align-items: center; // vertically center
	  justify-content: center; // Right align buttons with flex property because text-align doesn't work on flex items
	//   padding: 24px 16px;//$modal-inner-padding;
		padding:0 16px 24px;
	
	
	  @include border-bottom-radius($modal-content-border-radius);


	}

	// Measure scrollbar width for padding body during modal show/hide
	.cm-modal-scrollbar-measure {
	  position: absolute;
	  top: -9999px;
	  width: 50px;
	  height: 50px;
	  overflow: scroll;
	}

	// Scale up the modal
	@include media-breakpoint-up(sm) {
	  // Automatically set modal's width for larger viewports
	  .cm-modal-dialog {
	    max-width: $modal-md;
		margin: 200px auto;
	  }

	  .cm-modal-dialog-scrollable {
	    max-height: calc(100% - #{$modal-dialog-margin-y-sm-up * 2});

	    .cm-modal-content {
	      max-height: calc(100vh - #{$modal-dialog-margin-y-sm-up * 2});
	    }
	  }

	//   .cm-modal-dialog-centered {
	//     min-height: calc(100% - 160px);

	//     &::before {
	//       height: calc(100vh - 160px);
	//     }
	//   }

	  .cm-modal-content {
	    @include box-shadow($modal-content-box-shadow-sm-up);
	  }

	}


}


.cm-modal{
	.cm-modal-close{
		display: none;
		opacity:0;
		transform:scale(0);
		transition:opacity .2s, transform;
		// transform:scale(0);
		// transition:transform .8s cubic-bezier(0.420, 0.000, 0.710, 1.590); .5s;
		&.show{
			transform:scale(1) rotate(0deg);
			opacity:1;
		}
		outline: 0;
		cursor: pointer;
		line-height: 1;
		color: $close-color;
		background-color:rgba(255,255,255,1);
		width: 35px;
		height:35px;
		padding: 0;
		border: 0;
		border-radius:50%;
		@include media-breakpoint-up(lg) {
			margin-left:auto;
			margin-right:auto;
		}
		z-index: 50000;
		-webkit-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
		-moz-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
		box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
		&:before, &:after {
			position: absolute;
		    left: 16px;
		    content: ' ';
		    height: 20px;
		    width: 2px;
		    background-color: #4ba659;
		    top: 8px;
		}
		&:before {
			transform: rotate(45deg);
		}
		&:after {
			transform: rotate(-45deg);
		}

		@-webkit-keyframes rotate {
		    from { -webkit-transform: rotate(0deg) } 
		    to { -webkit-transform: rotate(360deg) } 
		}

		@include hover {
			background-color:rgba(255,255,255,1);
		    animation:rotate 1s  infinite;
			animation-timing-function: cubic-bezier(1,0,.5,1);
			&:before, &:after {
			    opacity:.7;
			}
		}

		&:hover{
			background-color:rgba(255,255,255,1);
		    animation:rotate 1s  infinite;
			animation-timing-function: cubic-bezier(1,0,.5,1);
			&:before, &:after {
			    opacity:.7;
			}
		}

		&:not(:disabled):not(.disabled) {
			@include hover-focus {
				opacity: 1;
			}
		}
	}
	
}

.cm-modal-footer {
	.cm-modal-btn{
		margin:10px;
	}
}

.cm-modal-footer.cm-modal-footer--full-btn {
	.cm-modal-btn{
		margin:0;
		width: 100%;
		+.cm-modal-btn{
			margin-top: 16px;
			margin-left: 0;
		}
	}
	
	@include media-breakpoint-up(md) {
		.cm-modal-btn{
			width: auto;
			+.cm-modal-btn{
				margin-top: 0;
				margin-left: 16px;
			}
		}
	}
}
@import "size";
@import "body";
@import "btn";