@import "../../bootstrap/functions";
@import "../../bootstrap/variables"; 
@import "../../bootstrap/mixins";
@import "../../mixins";

.cm-modal-btn,a.cm-modal-btn{
	@include btn('m');
	@include btn_size('m');
	@include btn_color();
	+.cm-modal-btn{
		margin-left: 10px;
	}
}
.cm-modal-btn--s,a.cm-modal-btn--s{
	@include btn_size('s');
}
.cm-modal-btn--l,a.cm-modal-btn--l{
	@include btn_size('lg');
}

.cm-modal-btn--white,a.cm-modal-btn--white{
	@include btn_color('white');
    &.cm-modal-btn--arrow-right{
        @include btn_icon('arrow-right',#888,8px,12px,2px,2px,-18px);
    }

    &:hover{
        &.cm-modal-btn--arrow-right{
            @include btn_icon('arrow-right',#00a94f,8px,12px,2px,2px,-18px);
        }
    }
}

.cm-modal-btn--orange,a.cm-modal-btn--orange{
	@include btn_color('orange');
    &.cm-modal-btn--arrow-right{
        @include btn_icon('arrow-right',#fff,8px,12px,2px,2px,-18px);
    }
}

.cm-modal-btn--green,a.cm-modal-btn--green{
	@include btn_color('green');
    &.cm-modal-btn--arrow-right{
        @include btn_icon('arrow-right',#fff,8px,12px,2px,2px,-18px);
    }
}
